"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { GoogleGenerativeAI } from "@google/generative-ai";

export default function Home() {
  const [text, setText] = useState("");
  const [word, setWord] = useState("");
  const router = useRouter();

  const genAI = new GoogleGenerativeAI(
    process.env.NEXT_PUBLIC_GEMINI_API_KEY
  );

  const highlightWithAI = async () => {
    try {
      const model = genAI.getGenerativeModel({
        model: "gemini-1.5-flash",
      });

      const prompt = `
      Highlight the word "${word}" in this text using <mark> tag:

      ${text}
      `;

      const result = await model.generateContent(prompt);
      const response = await result.response;
      const output = response.text();

      // Redirect to result page with response
      router.push(`/result?text=${encodeURIComponent(output)}`);
    } catch (err) {
      console.error(err);
      alert("Gemini API Error. Check console.");
    }
  };

  return (
    <div style={{ padding: 20 }}>
      <h1>AI Text Highlighter</h1>

      <textarea
        placeholder="Enter text"
        onChange={(e) => setText(e.target.value)}
        style={{ width: "100%", height: 120 }}
      />

      <input
        placeholder="Word to highlight"
        onChange={(e) => setWord(e.target.value)}
      />

      <br /><br />

      <button onClick={highlightWithAI}>
        Highlight Using Gemini
      </button>
    </div>
  );
}
